<template>
    <div class="author-tab">
        <div class="btn btn-default" style="position: absolute; right: 200px" @click="closeTab({id: author.id})">Close Tab</div>
        <div class="btn btn-default" style="position: absolute; right: 70px" @click="closeAllTabs()">Close All Tabs</div>
        <div class="basic-info">
            <img src='../assets/person.png'
                alt="Author Picture"
                height="300">
            <div class="author-info">
                <h1>{{author.name}}</h1>
                <h2>Contact: {{author.contact}}</h2>
                <h3>Birthplace: {{author.birthplace}}</h3>
                <h3>{{author.rating}}</h3>
                <h5>Brief Biography: </h5>
                <p>{{lorem}}</p>
            </div>
        </div>
        <div class="books">
            <h1>Books</h1>
            <book-list-item
                class="list-item"
                v-for="book in booksByAuth"
                :book = "book"
                :key="book.id"            
            ></book-list-item>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { mapState } from 'vuex'
import BookListItem from './BookListItem'

export default {
    name: 'AuthorTab',
    props: ['author'],
    components: {
        'book-list-item' : BookListItem
    },
    computed: {
        ...mapState({
            books: 'books',            
            lorem : 'lorem'
        }),
        booksByAuth(){
             var auth = this.author.name;
             return this.books.filter(function(book, index){
                 return book.author == auth;
             })   
        }        
    },
    methods:{
        ...mapMutations({
            removeFromTab: 'removeFromTabs',
            closeAllTabs: 'removeAllTabs'
        }),
        closeTab(id){
            this.removeFromTab(id);
            this.$emit('closed');
        }
    },
}
</script>

<style scoped>
.basic-info{
    padding: 30px;
    text-align: left;
    display: flex;
    flex-direction: row;

}

.author-info{
    padding: 5px;
}

.books{
    text-align: left;
    padding-left: 35px;
}

.author-tab{
    overflow: auto;
}

.list-item{
    min-height: 50px;
    padding: 10px;
    margin: 0px;
    border-bottom: 3px solid rgb(127, 114, 99);
}

.list-item:hover{
    cursor: pointer;
    background-color: #f9f6f2;
    font-size: 105%;
}
</style>



