<template>
    <div @click="createTab({elem: book})">
        <strong>{{book.title}}</strong>
        <p>by: {{book.author}}</p>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex'
    export default {
        name: 'BookListItem',
        props: ['book'],
        methods: {
            ...mapMutations({
                createTab: 'addToTabs'
            })
        }
    }
</script>

<style scoped>
    strong{
        font-size: 15px;
    }
</style>




