<template>
  <tabs class="tabs" v-model="activeTab" nav-style="tabs" justified>
    <div v-for="infotab in infotabs" :key="infotab.id">
        <div v-if="infotab.type == 'book'">
            <tab :header="infotab.title">
                <book-info :book="infotab"></book-info>
            </tab>
        </div>
        <div v-else>
            <tab :header="infotab.name">
                <author-info :author="infotab"></author-info>
            </tab>
        </div>
    </div>
  </tabs>  
</template>

<script>
import { tabs } from 'vue-strap'
import { tab } from 'vue-strap'
import { mapState } from 'vuex'
import BookTab from './BookTab'
import AuthorTab from './AuthorTab'

export default {
    name: 'InfoTabs',
    components: {
        'tabs' : tabs,
        'tab' : tab,
        'author-info' : AuthorTab,
        'book-info' : BookTab
    },
    data: () => {return{
    }},
    computed: {
        ...mapState({
            infotabs : 'tabs'
        }),
        activeTab(){
            if(this.infotabs){return this.infotabs.length - 1;}
        }
    },
    methods: {
    },
}
</script>

<style>

</style>



