<template>
    <div class="book-tab">
        <div class="btn btn-default" style="position: absolute; right: 200px" @click="closeTab({id: book.id})">Close Tab</div>
        <div class="btn btn-default" style="position: absolute; right: 70px" @click="closeAllTabs()">Close All Tabs</div>
        <div class="basic-info">
            <img src='../assets/book.svg'
                alt="Book Cover Image"
                height="400" width="300">
            <div class="book-info">
                <h1>{{book.title}}</h1>
                <h2>by: <span id="auth" @click="createTab({elem: author})">{{book.author}}</span></h2>
                <h4>Rating: {{book.rating}} out of 5</h4>
                <h5>Synopsis:</h5>
                <p>{{lorem}}</p>
            </div>
        </div>
        <div class="reviews">
            <h1>Reviews</h1>
            <strong>Anonymous {{book.rating}}/5</strong><div class="list-item">"{{lorem}}"</div>
            <strong>Critic {{book.rating - 1}}/5</strong><div class="list-item">"{{lorem}}"</div>
            <strong>Fan {{book.rating + 1}}/5</strong><div class="list-item">"{{lorem}}"</div>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { mapState } from 'vuex'

export default {
    name: 'BookTab',
    components: {
    },
    props: [
        'book'
    ],
    computed: {
        ...mapState({
            lorem : 'lorem',
            authors: 'authors'
        }),
        author: function(){
            for(var i = 0; i < this.authors.length; i++){
                if(this.authors[i].name == this.book.author){return this.authors[i]}
            }
        }
    },
    methods:{
        ...mapMutations({
            removeFromTab: 'removeFromTabs',
            createTab: 'addToTabs',
            closeAllTabs: 'removeAllTabs'            
        }),
        closeTab(bookid){
            this.removeFromTab(bookid);
            this.$emit('closed');
        }
    },
}
</script>

<style scoped>
.basic-info{
    padding: 30px;
    text-align: left;
    display: flex;
    flex-direction: row;
}

.book-info{
    padding: 5px;
}

.reviews{
    text-align: left;
    padding-left: 35px;
}

.book-tab{
    overflow: auto;
}

.list-item{
    min-height: 50px;
    padding: 20px;
    margin: 0px;
    border-bottom: 3px solid rgb(127, 114, 99);
}
.list-item:hover{
    background-color: #f9f6f2;
    cursor: default;
}

#auth:hover{
    cursor: pointer;
    text-decoration: underline;
}
</style>



