<template>
    <div id="search"> 
        <div id="search-bar">
            <span>
                <span style="float:left">
                    Search for: 
                    <span @click="search({text: searchtxt, filter: simpleSearchParams})"><input type="checkbox" id="title" value="Title" v-model="simpleSearchParams"> 
                    <label for="title">Book Title</label></span> 

                    <span @click="search({text: searchtxt, filter: simpleSearchParams})"><input type="checkbox" id="author" value="Author" v-model="simpleSearchParams">     
                    <label for="author">Author</label></span>  
                </span>
                <span style="float:right">
                </span>        
            </span>             
            <div @keyup="search({text: searchtxt, filter: simpleSearchParams})">
            <bs-input v-model="searchtxt" placeholder="Search"></bs-input>
            </div>

        </div>
        
        <div id="search-results">
            
            <div v-if="searchByTitle">
                <search-list type="book">Book</search-list>
            </div>
            <div v-if="searchByAuthor">
                <search-list type="author">Author</search-list>
            </div>
        </div>
    </div>
</template>

<script>


    window.addEventListener("scroll", function(e){
    var searchElem = document.getElementById('search');
    var positionInfo = searchElem.getBoundingClientRect();
    var sEWidth = positionInfo.width;
        if(window.scrollY > 130){
            searchElem.style.position = 'fixed';
            searchElem.style.top = '0';
            searchElem.style.width = sEWidth + 'px';            
        }else{
            searchElem.style.position = '';
            searchElem.style.top = '';
            searchElem.style.width = '';
        }
    });

    import { input } from 'vue-strap'
    import SearchList from './SearchList'
    import { mapState } from 'vuex'
    import { mapMutations } from 'vuex'

    export default {
        name: 'Search',
        components: {
           'bs-input' : input,
           'search-list': SearchList
        },        
        data: () => {return{
            searchtxt: '',
            simpleSearchParams: ['Title', 'Author']
        }},
        computed: {
            searchByTitle: function(){
                var checkboxChecked = this.simpleSearchParams ? this.simpleSearchParams.indexOf("Title") > -1  : false;
                var hasResults = this.bookResults.length > 0;
                return checkboxChecked && hasResults;
            },

            searchByAuthor: function(){
                var checkboxChecked = (this.simpleSearchParams) ? this.simpleSearchParams.indexOf("Author") > -1 : false;
                var hasResults = this.authorResults.length > 0;
                return checkboxChecked && hasResults;
            },

            ...mapState({
                bookResults: 'bookResults',
                authorResults: 'authorResults'
            })
        },
        methods: {
            ...mapMutations({
                search: 'createList'
            }),
        }

    }
</script>

<style scoped>
#search-bar{
    padding: 5px;
    background-color: #f2eae3;
}
#search-bar span{
    margin-left: 5px;
}
#search{
    padding: 10px;
    height: 20px;
    text-align: left;
}
</style>


