<template>
  <div id="app">
    <app-header></app-header>
    <div class="col-md-4"><search></search></div>
    <div class="col-md-8">
      <div id="flex-container">
        <div v-if="dataAvailable" style="align-items: flex-start">
          <info-tabs class="tabs"></info-tabs>
        </div>
        <div v-else style="margin-top: 200px">
          <h1> SEARCH AND SELECT A BOOK OR AUTHOR </h1>
        </div>
      </div>
    </div>

  </div>
</template>

<script>

import AppHeader from './components/AppHeader'
import Search from './components/Search'
import InfoTabs from './components/InfoTabs'
import { mapState } from 'vuex'


export default {
  name: 'App',
  components: {
    'app-header' : AppHeader,
    'search' : Search,
    'info-tabs' : InfoTabs
  },
  data: () => {return{
    activeTab : 0,
  }},
  computed: {
    dataAvailable : function(){return this.tabs.length > 0},
    ...mapState({
        tabs : 'tabs'
    })
  }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}

#flex-container{
  background-color: #f2eae3;
  width: 100%;
  min-height: 800px;
  margin: 10px;
  display: flex;
  justify-content: center;
  color: rgb(127, 114, 99);
  font-family: 'Bookman Old Style'
}

.tabs{
  margin: 20px;
  padding: 10px;
  border-radius: 5px;
  background-color: white;
  min-height: 700px;
}
</style>



