<template>
    <div class="search-list">
        <div>
            <strong class="panel-header">
                <div><slot></slot></div>
            </strong>
                <div v-if="type == 'book'" class="list">
                    <book-list-item
                        class="list-item"
                        v-for="book in books"
                        :book = "book"
                        :key="book.id"
                    >BOOK</book-list-item>
                </div>

                <div v-if="type == 'author'" class="list">
                    <author-list-item
                       class="list-item"
                       :author = "author"
                       v-for="author in authors"
                       :key="author.id"
                    >AUTHOR</author-list-item>
                </div>            
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import BookListItem from './BookListItem'
    import AuthorListItem from './AuthorListItem'

    export default {
        name: 'SearchList',
        components: {
            'book-list-item' : BookListItem,
            'author-list-item' : AuthorListItem
        },
        props:[
            'type'
        ],
        computed: {
            ...mapState({
                books: 'bookResults',
                authors: 'authorResults' 
            })
        },
        
    }
</script>

<style scoped>
    .panel-header{
        display: flex;
        justify-content: space-between;
        background-color: #f2eae3; 
        padding: 10px;
    }
    .search-list{
        margin-top: 5px;    
        border: 2px solid #f2eae3;   
        border-radius: 5px;
    }
    .search-list .list{
        max-height: 275px;
        overflow: auto;
        margin: 0px;
    }
    .list-item{
        min-height: 50px;
        padding: 10px;
        margin: 0px;
        border-bottom: 3px solid rgb(127, 114, 99);
    }

    .list-item:hover{
        cursor: pointer;
        background-color: #f9f6f2;
        font-size: 105%;
    }
</style>



