import Vue from 'vue'
import Vuex from 'vuex'
import books from './booksdata'
import authors from './authdata'


Vue.use(Vuex)

const state = {
    // Data 
    lorem: `Lorem ipsum dolor sit amet,consectetur adipiscing elit,
    sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
    Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris 
    nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in 
    reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla 
    pariatur. Excepteur sint occaecat cupidatat non proident, sunt in 
    culpa qui officia deserunt mollit anim id est laborum`,

    books: books,
    authors: authors,

    // Store
    bookResults : [],
    authorResults : [],
    tabs : []
};

const getters = {
};

const mutations = {
    createList(store, payload){
        store.bookResults = [];
        store.authorResults = [];

        var newBookArr = [];
        var newAuthArr = [];

        if(payload.filter.includes('Title')){
            newBookArr = store.books.filter(function(book, index){
               return  book.title.toLowerCase().indexOf(payload.text.toLowerCase()) > -1
            });
            store.bookResults = newBookArr;
        }

        if(payload.filter.includes('Author')){
            newAuthArr = store.authors.filter(function(author, index){
               return  author.name.toLowerCase().indexOf(payload.text.toLowerCase()) > -1
            });
            store.authorResults = newAuthArr;
        }
    },

    addToTabs(store, payload){ // payload: type(author or book), id
        for(var i = 0; i < store.tabs.length; i++){
            if(store.tabs[i].id == payload.elem.id){return;}
        }
        store.tabs.push(payload.elem);
    },
    removeFromTabs(store, payload){ // payload: type(author or book), id
        for(var i = 0; i < store.tabs.length; i++){
            if(store.tabs[i].id == payload.id){
                store.tabs.splice(i, 1);
                return;
            }
        }
    },
    removeAllTabs(store){
        store.tabs = [];
    }

};

const actions = {
    getAuthorList(context){},
    getBookList(){},
    getAuthorInfo(){},
    getBookInfo(){}
};

const store = new Vuex.Store({
    state, getters, mutations, actions
});

export default store;



