<template>
      <div  @click="createTab({type: 'author', elem: author})">
        <strong>{{author.name}}</strong>
        <p style="font-weight: bold">contact: {{author.contact}}</p>
        Books By This Author:
        <li v-for="bookByAuth in booksByAuth" :key="bookByAuth.id">{{bookByAuth.title}}</li>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { mapMutations } from 'vuex'
    export default {
        name: 'AuthorListItem',
        props: ['author'],
        computed: {
            ...mapState({
                books: 'books'
            }),
            booksByAuth(){
             var auth = this.author.name;
             return this.books.filter(function(book, index){
                 return book.author == auth;
             })   
            }
        },
        methods: {
            ...mapMutations({
                createTab: 'addToTabs'
            })
        }
    }
</script>

<style>
    strong{
        font-size: 15px;
    }
</style>




